import React, { useEffect, useState } from 'react';
import { Button, Form, Grid, Header, Message } from 'semantic-ui-react';
import {
  API,
  generateToken,
  showError,
  showSuccess,
  testChannel,
} from '../helpers';
import { loadUser, loadUserChannels } from '../helpers/loader';

const PushSetting = () => {
  let [user, setUser] = useState({
    id: '',
    username: '',
    channel: '',
    token: '',
  });
  let [channels, setChannels] = useState([]);
  let [loading, setLoading] = useState(true);

  const handleInputChange = (e, { name, value }) => {
    setUser((inputs) => ({ ...inputs, [name]: value }));
  };

  useEffect(() => {
    const loader = async () => {
      let user = await loadUser();
      if (user) {
        setUser(user);
      }
      let channels = await loadUserChannels();
      if (channels) {
        setChannels(channels);
      }
      setLoading(false);
    };
    loader().then();
  }, []);

  const submit = async (which) => {
    let data = {};
    switch (which) {
      case 'general':
        data.channel = user.channel;
        data.token = user.token;
        if (data.token === '') {
          data.token = ' ';
        }
        break;
      default:
        showError(`${which}`);
        return;
    }
    let res = await API.put(`/api/user/self`, data);
    const { success, message } = res.data;
    if (success) {
      showSuccess('');
    } else {
      showError(message);
    }
  };

  return (
    <Grid columns={1}>
      <Grid.Column>
        <Form loading={loading}>
          <Header as='h3'></Header>
          <Message>
            
          </Message>
          <Form.Group>
            <Form.Select
              label=''
              name='channel'
              options={channels}
              value={user.channel}
              onChange={handleInputChange}
              width={5}
            />
            <Form.Input
              label=''
              placeholder=''
              value={user.token}
              name='token'
              onChange={handleInputChange}
              width={9}
              action={{
                content: '',
                onClick: () => {
                  console.log('generate token');
                  setUser((inputs) => ({
                    ...inputs,
                    token: generateToken(16),
                  }));
                },
              }}
            />
          </Form.Group>
          <Button onClick={() => submit('general')} loading={loading}>
            
          </Button>
          <Button onClick={() => testChannel(user.username, user.token, '')}>
            
          </Button>
        </Form>
      </Grid.Column>
    </Grid>
  );
};

export default PushSetting;
